import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'
import store from './store'
import {
	$http
} from '@escook/request-miniprogram'
import Vue from 'vue'
import GoEasy from '@/uni_modules/GOEASY-IM/js_sdk/goeasy-2.12.4.esm.min.js'
Vue.config.productionTip = false
App.mpType = 'app'
import uView from "uview-ui";
Vue.use(uView);
// 引入全局TuniaoUI
import TuniaoUI from 'tuniao-ui'
Vue.use(TuniaoUI)
uni.$http = $http
// $http.baseUrl = 'https://localhost'
$http.baseUrl = 'https://www.wangzh.work'
// 引入TuniaoUI提供的vuex简写方法
let vuexStore = require('@/store/$t.mixin.js')
Vue.mixin(vuexStore)

// 引入TuniaoUI对小程序分享的mixin封装
let mpShare = require('tuniao-ui/libs/mixin/mpShare.js')
Vue.mixin(mpShare)
$http.beforeRequest = function(options) {
	uni.showLoading({
		title: "加载中..."
	})
	options.header = {
		'token': uni.getStorageSync('token'),
	}
}

Vue.prototype.ScanAudio = function(path) {
	var music = null;
	music = uni.createInnerAudioContext(); //创建播放器对象 
	music.src = path;  
	music.play();
	music.onEnded(() => {
		music = null;	
	});
}
$http.afterRequest = function(options) {
	if (options.statusCode === 401) {
		uni.removeStorageSync('token');
		uni.removeStorageSync('userId');
		uni.redirectTo({
			url: '/pages/login/login'
		})
		uni.showToast({
			title: '登录失效~',
			icon: 'error'
		})
	}
	uni.hideLoading()
}
uni.$showMsg = function(title = '数据加载失败', duration = 1500) {
	uni.showToast({
		title,
		duration,
		icon: 'none'
	})
}
GoEasy.init({
    host:"hangzhou.goeasy.io",
    appkey:"BC-8da4348c79f04d33bad09f8c1be2f051",
    modules: ['im']
});

uni.$GoEasy = GoEasy;

GoEasy.onClickNotification((message) => {
    let currentUrl;
    const routes = getCurrentPages();

    if (routes && routes.length) {
        const curRoute = routes[routes.length - 1].route;
        const curParam = routes[routes.length - 1].options;
        currentUrl = '/' + curRoute + `?to=${curParam.to}`;
    }

    let newUrl;
    switch (message.toType) {
        case GoEasy.IM_SCENE.PRIVATE:
            newUrl = '/messagePages/privateChat?to=' + message.senderId;
            break;
        case GoEasy.IM_SCENE.GROUP:
            newUrl = '/messagePages/groupChat?to=' + message.groupId;
            break;
    }

    if (currentUrl !== newUrl) {
        uni.navigateTo({
            url: newUrl,
        });
    }

});
const app = new Vue({
	store,
	...App
})

app.$mount()